header {
    height: 100vh;
    padding-top: 7rem;
    position: relative;
    overflow: hidden;
  }
  
  .header__container {
    text-align: center;
    height: 100%;
    position: relative;
    animation: fadeIn 1.5s ease;
  }
  
  /* Animaciones generales */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* CTA */
  .cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.5rem;
    justify-content: center;
  }
  
  .cta .btn {
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 5px;
    transition: all 0.3s ease;
  }
  
  .cta .btn:hover {
    background-color: var(--color-primary);
    color: white;
  }
  
  /* Redes Sociales */
  .header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.2rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    animation: slideIn 1.5s ease forwards;
  }
  
  .header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
  }
  
  .social-link {
    transition: transform 0.3s ease, color 0.3s ease;
  }
  
  .social-link:hover {
    transform: translateY(-3px);
    color: var(--color-primary);
  }
  
  /* Imagen de perfil */
  .me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
    transition: transform 0.3s ease;
  }
  
  .me:hover {
    transform: translateY(-10px);
  }
  
  .me img {
    -webkit-user-select: none;
    user-select: none;
  }
  
  /* Botón de scroll */
  .scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    transition: all 0.3s ease;
  }
  
  .scroll__down:hover {
    color: var(--color-primary);
    transform: rotate(90deg) translateX(5px);
  }
  
  /* Media Queries */
  @media screen and (max-width: 1024px) {
    .header__socials {
      flex-direction: row;
      justify-content: center;
      bottom: 1.5rem;
    }
  }
  
  @media screen and (max-width: 600px) {
    .header__socials {
      display: none;
    }
  
    .me {
      width: 18rem;
      height: 24rem;
      left: calc(50% - 9rem);
    }
  }