.about__container {
    display: grid;
    grid-template-columns: 35% 55%;
    gap: 10%;
    align-items: center;
    animation: fadeInUp 1.5s ease-in-out;
}

.about__me {
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(135deg, var(--color-primary), transparent);
    display: grid;
    place-items: center;
    margin: 0 auto;
}

.about__me-image {
    border-radius: 1rem;
    overflow: hidden;
    transform: rotate(-10deg);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.about__me-image:hover {
    transform: rotate(0);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
}

.about__me-image img {
    -webkit-user-select: none;
    user-select: none;
    width: 100%;
    height: auto;
}

/* Cards Section */
.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    margin-top: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1.5rem;
    text-align: center;
    transition: transform 0.3s ease, background 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.about__card:hover {
    transform: translateY(-5px);
    background: transparent;
    border-color: var(--color-primary);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
}

.about__icon {
    color: var(--color-primary);
    font-size: 2rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 1rem;
    font-weight: 600;
}

.about__card small {
    font-size: 0.8rem;
    color: var(--color-light);
}

/* Text Section */
.about__content p {
    margin: 1rem 0;
    color: var(--color-light);
    line-height: 1.5;
}

/* Animations */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Responsive */
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
    }

    .about__me {
        width: 60%;
        margin: 2rem auto;
    }

    .about__cards {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
}

@media screen and (max-width: 600px) {
    .about__me {
        width: 80%;
    }

    .about__cards {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }
}