.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    padding: 1rem;
  }
  
  .portfolio__item {
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
  }
  
  .portfolio__item:hover {
    transform: translateY(-10px);
    border-color: var(--color-primary);
    background: transparent;
    box-shadow: 0 10px 20px rgba(0,0,0,0.2);
  }
  
  .portfolio__item-image {
    position: relative;
    border-radius: 1.5rem;
    overflow: hidden;
  }
  
  .portfolio__item-image img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  
  .portfolio__item:hover .portfolio__item-image img {
    transform: scale(1.1);
  }
  
  .hover-overlay {
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .portfolio__item:hover .hover-overlay {
    opacity: 1;
  }
  
  .hover-overlay span {
    color: white;
    font-size: 1.1rem;
    padding: 0.8rem 1.5rem;
    border: 2px solid white;
    border-radius: 2rem;
  }
  
  /* Preview Modal */
  .preview-modal {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    animation: fadeIn 0.3s ease;
  }
  
  .modal-content {
    background: var(--color-bg);
    border-radius: 2rem;
    width: 90%;
    max-width: 800px;
    max-height: 90vh;
    overflow-y: auto;
    position: relative;
    animation: slideUp 0.3s ease;
  }
  
  .close-btn {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: none;
    border: none;
    color: var(--color-primary);
    font-size: 2rem;
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  
  .close-btn:hover {
    transform: rotate(90deg);
  }
  
  .modal-image img {
    width: 100%;
    height: 300px;
    object-fit: cover;
  }
  
  .modal-info {
    padding: 2rem;
  }
  
  .tech-stack {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    margin: 1rem 0;
  }
  
  .tech-tag {
    background: var(--color-primary);
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 1rem;
    font-size: 0.9rem;
  }
  
  .features ul {
    margin-left: 1.5rem;
    margin-top: 0.5rem;
  }
  
  .features li {
    margin-bottom: 0.5rem;
  }
  
  /* Animaciones */
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  @keyframes slideUp {
    from { 
      opacity: 0;
      transform: translateY(50px);
    }
    to { 
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Media Queries */
  @media screen and (max-width: 1228px) {
    .portfolio__container {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  @media screen and (max-width: 710px) {
    .portfolio__container {
      grid-template-columns: 1fr;
    }
  
    .modal-content {
      width: 95%;
    }
  }