@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}


/* Light Theme */
:root {
  --color-bg: #f4f4f9;
  --color-bg-variant: #e4e4ee;
  --color-primary: #4db5ff;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #1f1f38;
  --color-light: rgba(31, 31, 56, 0.6);
}

/* Dark Theme */
body.dark-mode {
  --color-bg: #1f1f38;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4db5ff;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);
}

/* General Body Styling */
body {
  background: var(--color-bg);
  color: var(--color-white);
  transition: background 0.3s ease, color 0.3s ease;
}


/* Button Styles */
.theme-toggle {
  position: fixed;
  top: 1rem;
  right: -2rem;
  background: var(--color-primary);
  color: var(--color-bg);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 1.2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  transition: background 0.3s ease, color 0.3s ease;
}

.theme-toggle:hover {
  background: var(--color-bg-variant);
  color: var(--color-white);
}



html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    background: #1f1f4b;
    width: 0.8rem;
}

::-webkit-scrollbar-thumb {
    background: #3783ba;
    border-radius: 4px;
    width: 0.8rem;
}


/* ============= GENERAL STYLES ============= */
.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5{
    font-weight: 600;
    cursor: default;
}

h1{
    font-size: 2.5rem;
}

p{
    cursor: default;
}

a{
    user-select: none;
}

section{
    margin-top: 10rem;
}

section > h2, section > h5{
    text-align: center;
    color: var(--color-white);
}

section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ============= MEDIA QUERIES (MEDIUM DEVICES) ============= */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section{
        margin-top: 8rem;
    }
}

/* ============= MEDIA QUERIES (SMALL DEVICES) ============= */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2{
        margin-bottom: 2rem;
        height: 4vh;
}
}


/* Preloader Container */
.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, var(--color-primary), var(--color-bg));
    color: var(--color-white);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    animation: fadeOut 1s ease 3s forwards; /* Fades out after 3 seconds */
  }
  
  /* Loading Dots Animation */
  .preloader__animation {
    display: flex;
    gap: 0.8rem;
    margin-bottom: 1.5rem;
  }
  
  .dot {
    width: 15px;
    height: 15px;
    background-color: var(--color-white);
    border-radius: 50%;
    animation: bounce 1.5s infinite ease-in-out;
  }
  
  .dot:nth-child(1) {
    animation-delay: 0s;
  }
  
  .dot:nth-child(2) {
    animation-delay: 0.2s;
  }
  
  .dot:nth-child(3) {
    animation-delay: 0.4s;
  }
  
  /* Bounce Animation */
  @keyframes bounce {
    0%, 80%, 100% {
      transform: scale(0);
    }
    40% {
      transform: scale(1);
    }
  }
  
  /* Fade Out Animation */
  @keyframes fadeOut {
    to {
      opacity: 0;
      visibility: hidden;
    }
  }
  
  /* Preloader Heading */
  .preloader h3 {
    font-size: 1.5rem;
    font-weight: 600;
    text-align: center;
    letter-spacing: 0.05em;
    animation: textFadeIn 2s ease forwards;
  }
  
  /* Text Fade In Animation */
  @keyframes textFadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }