/* Navigation Bar */
nav {
  background: var(--color-bg-variant);
  width: max-content;
  display: flex;
  align-items: center;
  justify-content: space-evenly; /* Distribuir uniformemente */
  padding: 1rem 1.5rem;
  z-index: 10;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  gap: 1rem; /* Espaciado entre elementos */
  border-radius: 3rem;
  backdrop-filter: blur(15px);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease-in-out, background 0.3s ease;
}

/* Navigation Links */
nav a {
  background: transparent;
  padding: 0.8rem;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color-light);
  font-size: 1.5rem;
  transition: background 0.3s ease, color 0.3s ease, transform 0.3s ease;
}

nav a:hover {
  background: rgba(255, 255, 255, 0.2);
  color: var(--color-primary);
  transform: scale(1.2);
}

nav a.active {
  background: var(--color-primary);
  color: var(--color-bg);
  transform: scale(1.3);
}

/* Botón de Tema */
.theme-toggle {
  background: transparent;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color-primary);
  border: 2px solid var(--color-primary);
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease, color 0.3s ease;
  right: -5rem;
}

.theme-toggle:hover {
  background: var(--color-primary);
  color: var(--color-bg);
  transform: scale(1.2);
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  nav {
    padding: 0.8rem 1.2rem;
    gap: 0.8rem;
  }

  nav a {
    font-size: 1.2rem;
    padding: 0.7rem;
  }

  .theme-toggle {
    width: 45px;
    height: 45px;
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  nav {
    padding: 0.5rem 1rem;
    bottom: 1.5rem;
    gap: 0.6rem;
  }

  nav a {
    font-size: 1rem;
    padding: 0.6rem;
  }

  .theme-toggle {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }
}
